import React from "react";
import { data } from "./data";

const FaqData = () => {
  return (
    <div className="block w-100 single-page my-4 mx-auto">
      <div className="flex flex-ycenter pb-1 flex-start single__desc flex-column">
        <div className="bold">FAQ</div>
        <br />
        {data.map((m, i) => {
          const { question, answer } = m;
          return (
            <div key={i}>
              <strong>
                {question}
                <br />
              </strong>
              <span dangerouslySetInnerHTML={{ __html: answer }} />
              <br />
              <br />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default FaqData;



