import React, { Component } from "react";
import ReactTooltip from "react-tooltip";
// file upload modal form
import axios from "axios";
import firebase from "firebase";
import "firebase/storage";
import FileUploader from "react-firebase-file-uploader";
import ReCAPTCHA from "react-google-recaptcha";
import MediaQuery from "react-responsive";
// modal
import Modal from "../../components/Modal";

// import Typing from '../../components/typing';

// import { getRandomInt } from '../../utils/numbers';
import {
  firebaseConfig,
  airtableConfig,
  recaptchaConfig
} from "../../data/configData";
import { applicationModalData as amd } from "../../data/data";

import "./style.scss";

let captcha;
// initialize firebase app
firebase.initializeApp(firebaseConfig);

export default class Hero extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formModal: false,
      firstName: "",
      lastName: "",
      email: "",
      github: "",
      linkedIn: "",
      comment: "",
      city: "",
      cvIsUploading: false,
      cvProgress: null,
      cvName: "",
      cvUrl: "",
      cvError: "",
      clIsUploading: false,
      clProgress: null,
      clName: "",
      clUrl: "",
      clError: "",
      uploadMessage: "",
      uploadStatus: "",
      disabled: true,
      showButton: false,
      isEmailValid: null
    };
  }

  onRecaptchaChange = () => {
    const disabled = this.checkDisabled("showButton");
    this.setState({ showButton: true, disabled });
  };

  onFormSubmit = () => {
    const {
      firstName,
      lastName,
      city,
      email,
      github,
      linkedIn,
      comment,
      cvUrl,
      clUrl
    } = this.state;
    const temp = firstName && lastName && city && email && cvUrl;
    if (city.toLowerCase().trim() === "sarajevo" ? temp : temp) {
      const fullName = `${firstName} ${lastName}`;
      axios
        .post(
          airtableConfig.postUrl,
          {
            fields: {
              Attachments: [
                {
                  url: cvUrl,
                  filename: `${fullName} CV`
                },
                {
                  url: clUrl,
                  filename: `${fullName} Cover Letter`
                }
              ],
              "Email Address": email,
              "First Name": firstName,
              "Last Name": lastName,
              "Full Name": fullName,
              City: city,
              "LinkedIn Account": linkedIn,
              "Github Account": github,
              "Additional comments and references": comment
            }
          },
          airtableConfig.headers
        )
        .then(() =>
          this.setState({
            uploadMessage: amd.success,
            uploadStatus: "success"
          })
        )
        .catch(() =>
          this.setState({
            uploadMessage: amd.error,
            uploadStatus: "error"
          })
        );
    } else {
      this.setState({
        uploadMessage: amd.error,
        uploadStatus: "error"
      });
    }
  };
  getButtonTooltip = () => {
    if (this.state.uploadStatus === "" && this.state.disabled) {
      return "Please fill required (*) fields and check recaptcha.";
    } else if (this.state.uploadStatus === "success") {
      return "Your application has already been successful. No need to do it twice.";
    }
    return undefined;
  };

  openFormModal = () => this.setState({ formModal: true });

  closeFormModal = () => {
    captcha.reset();
    this.setState({
      formModal: false,
      firstName: "",
      lastName: "",
      email: "",
      github: "",
      linkedIn: "",
      comment: "",
      city: "",
      cvIsUploading: false,
      cvProgress: null,
      cvUrl: "",
      cvName: "",
      cvError: "",
      clIsUploading: false,
      clProgress: null,
      clUrl: "",
      clName: "",
      clError: "",
      uploadMessage: "",
      uploadStatus: "",
      disabled: true,
      showButton: false
    });
  };

  validateEmail = email => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  };

  hasValue = item => item !== "";

  checkDisabled = (item, value = "") => {
    const {
      firstName,
      lastName,
      city,
      email,
      cvUrl,
      clUrl,
      showButton
    } = this.state;
    const mailIsValid = this.validateEmail(item === "email" ? value : email);
    const hasValue = this.hasValue(value);
    const checking =
      (item === "firstName" ? hasValue : this.hasValue(firstName)) &&
      (item === "lastName" ? hasValue : this.hasValue(lastName)) &&
      (item === "city" ? hasValue : this.hasValue(city)) &&
      (item === "cv" ? hasValue : this.hasValue(cvUrl)) &&
      (item === "cl" ? hasValue : this.hasValue(clUrl)) &&
      (item === "showButton" || showButton);
    return !(checking && mailIsValid);
  };

  // handle form input change value
  handleChangeInput = event => {
    const disabled = this.checkDisabled(event.target.name, event.target.value);
    this.setState({ [event.target.name]: event.target.value, disabled });
  };
  handleOnEmailChange = event => {
    const disabled = this.checkDisabled(event.target.name, event.target.value);
    if (this.state.isEmailValid !== null) {
      const valid = this.validateEmail(event.target.value);
      this.setState({
        [event.target.name]: event.target.value,
        disabled,
        isEmailValid: valid
      });
    } else {
      this.setState({ [event.target.name]: event.target.value, disabled });
    }
  };
  handleOnBlur = event => {
    const valid = this.validateEmail(event.target.value);
    this.setState({ isEmailValid: valid });
  };
  // file upload methods
  handleUploadStart = (event, item) => {
    this.setState({
      [`${item}IsUploading`]: true,
      [`${item}Progress`]: 0,
      [`${item}Name`]: event.name
    });
  };

  handleProgress = (progress, item) =>
    this.setState({ [`${item}Progress`]: progress });

  handleUploadError = (error, item) =>
    this.setState({ [`${item}IsUploading`]: false, [`${item}Error`]: error });

  handleUploadSuccess = (filename, item) => {
    firebase
      .storage()
      .ref(firebaseConfig.projectId)
      .child(filename)
      .getDownloadURL()
      .then(url => {
        const disabled = this.checkDisabled(item, url);
        this.setState({
          [`${item}IsUploading`]: false,
          [`${item}Progress`]: 100,
          [`${item}Url`]: url,
          disabled
        });
      });
  };

  render() {
    return (
      <div className="modal-form">
        <Modal
          visible={this.state.formModal}
          onClose={this.closeFormModal}
          measure="%"
          width={70}
          height={70}
        >
          <h4 className="mb-2">Application form</h4>
          <div className="flex flex-gap-2 mb-2">
            <div className="form-group w-50">
              <label htmlFor="firstName" className="block mb-1">
                First name*:{" "}
              </label>
              <input
                className="form-control"
                type="text"
                value={this.state.firstName}
                name="firstName"
                placeholder="Charles"
                id="firstName"
                onChange={this.handleChangeInput}
                required
              />
            </div>
            <div className="form-group w-50">
              <label htmlFor="lastName" className="block mb-1">
                Last name*:{" "}
              </label>
              <input
                className="form-control"
                type="text"
                value={this.state.lastName}
                name="lastName"
                placeholder="Xavier"
                id="lastName"
                onChange={this.handleChangeInput}
                required
              />
            </div>
          </div>
          <div className="flex flex-gap-2 mb-2">
            <div
              className={`form-group w-50 ${this.state.city
                .toLowerCase()
                .trim() !== "sarajevo" && "pr-1"}`}
            >
              <label htmlFor="city" className="block mb-1">
                City*:{" "}
              </label>
              <input
                className="form-control"
                type="text"
                value={this.state.city}
                name="city"
                placeholder="New York"
                id="city"
                onChange={this.handleChangeInput}
                required
              />
            </div>
          </div>
          <div className="flex flex-gap-2 mb-2">
            <div className="form-group w-50">
              <label htmlFor="linkedIn" className="block mb-1">
                Linkedin:{" "}
              </label>
              <input
                className="form-control"
                type="url"
                value={this.state.linkedIn}
                name="linkedIn"
                placeholder="https://www.linkedIn.com/in/charles-xavier-7541684/"
                id="linkedIn"
                onChange={this.handleChangeInput}
              />
            </div>
            <div className="form-group w-50">
              <label htmlFor="github" className="block mb-1">
                Github:{" "}
              </label>
              <input
                className="form-control"
                type="url"
                value={this.state.github}
                name="github"
                placeholder="https://github.com/X-Men"
                id="github"
                onChange={this.handleChangeInput}
              />
            </div>
          </div>
          <div className="flex flex-gap-2 mb-2">
            <div
              className={`form-group w-50 ${
                this.state.isEmailValid === false ? "email-error" : ""
              } pr-1`}
            >
              <label htmlFor="email" className="block mb-1">
                Email*:{" "}
              </label>
              <input
                className="form-control"
                type="email"
                value={this.state.email}
                name="email"
                placeholder="charles.xavier@x.man"
                id="email"
                onChange={this.handleOnEmailChange}
                onBlur={this.handleOnBlur}
                required
              />
              {this.state.isEmailValid === false && (
                <span className="bold">
                  E-mail is not valid. Please enter valid e-mail.
                </span>
              )}
            </div>
          </div>
          <div className="flex flex-gap-2 mb-2">
            <div className="form-group w-50">
              <label htmlFor="comment" className="block mb-1">
                Additional comments and references:{" "}
              </label>
              <textarea
                className="form-control"
                value={this.state.comment}
                name="comment"
                placeholder="Additional comments and references"
                id="comment"
                onChange={this.handleChangeInput}
              />
            </div>
          </div>
          <div className="flex flex-gap-2 mb-2">
            <div className="form-group w-50">
              <label
                htmlFor="cv"
                className=" flex flex-center mb-1 upload_button p-2"
              >
                CV*
                <FileUploader
                  className="form-control"
                  accept="application/msword, text/plain, application/pdf"
                  name="cv"
                  id="cv"
                  hidden
                  randomizeFilename
                  storageRef={firebase.storage().ref(firebaseConfig.projectId)}
                  onUploadStart={e => this.handleUploadStart(e, "cv")}
                  onUploadError={error => this.handleUploadError(error, "cv")}
                  onUploadSuccess={filename =>
                    this.handleUploadSuccess(filename, "cv")
                  }
                  onProgress={progress => this.handleProgress(progress, "cv")}
                />
              </label>
              <p className="block break-word">{this.state.cvName}</p>
              {this.state.cvIsUploading && (
                <p className="block mt-2">Progress: {this.state.cvProgress}</p>
              )}
            </div>
            <div className="form-group w-50">
              <label
                htmlFor="coverLetter"
                className="flex flex-center mb-1 upload_button p-2"
              >
                Cover letter*
                <FileUploader
                  accept="application/msword, text/plain, application/pdf"
                  name="coverLetter"
                  id="coverLetter"
                  hidden
                  randomizeFilename
                  storageRef={firebase.storage().ref(firebaseConfig.projectId)}
                  onUploadStart={e => this.handleUploadStart(e, "cl")}
                  onUploadError={error => this.handleUploadError(error, "cl")}
                  onUploadSuccess={filename =>
                    this.handleUploadSuccess(filename, "cl")
                  }
                  onProgress={progress => this.handleProgress(progress, "cl")}
                />
              </label>
              <p className="block break-word cover-letter">
                Make sure your cover letter is personal, representable and
                honest. Tell us why is this program for you, when did you
                discover your passion for technology and how would you fit in
                Mistral among other things. This letter is the main elimination
                criteria.
              </p>
              <p className="block break-word">{this.state.clName}</p>
              {this.state.clIsUploading && (
                <p className="block mt-2">Progress: {this.state.clProgress}</p>
              )}
            </div>
          </div>
          {this.state.uploadMessage && (
            <div className={`bold ${this.state.uploadStatus} mb-3`}>
              {this.state.uploadMessage}
            </div>
          )}
          <div className="flex flex-gap-2 mb-2">
            <div className="w-50 recptcha-wrapper my-2">
              <MediaQuery minWidth={976}>
                <ReCAPTCHA
                  ref={el => {
                    captcha = el;
                  }}
                  sitekey={recaptchaConfig.siteKey}
                  onChange={this.onRecaptchaChange}
                />
              </MediaQuery>
              <MediaQuery minWidth={768} maxWidth={976}>
                <ReCAPTCHA
                  ref={el => {
                    captcha = el;
                  }}
                  size="compact"
                  sitekey={recaptchaConfig.siteKey}
                  onChange={this.onRecaptchaChange}
                />
              </MediaQuery>
              <MediaQuery minWidth={480} maxWidth={768}>
                <ReCAPTCHA
                  ref={el => {
                    captcha = el;
                  }}
                  sitekey={recaptchaConfig.siteKey}
                  onChange={this.onRecaptchaChange}
                />
              </MediaQuery>
              <MediaQuery maxWidth={480}>
                <ReCAPTCHA
                  ref={el => {
                    captcha = el;
                  }}
                  size="compact"
                  sitekey={recaptchaConfig.siteKey}
                  onChange={this.onRecaptchaChange}
                />
              </MediaQuery>
            </div>
            <ReactTooltip />
            <div className="w-50 flex flex-center my-2">
              <button
                className={`btn form__btn p-3 w-100 ${
                  this.state.disabled ? "disabled" : ""
                }`}
                onClick={
                  this.state.disabled || this.state.uploadStatus !== ""
                    ? undefined
                    : this.onFormSubmit
                }
                data-tip={this.getButtonTooltip()}
              >
                Apply
              </button>
            </div>
          </div>
        </Modal>
        <div
          className={`hero relative white ${
            this.props.removeBgOverlay ? "remove-overlay" : ""
          }`}
          style={{
            backgroundImage: `url("/img/mentors/${this.props.bgImage}.jpg")`
          }}
        >
          {!this.props.removeText && (
            <div className="hero__inner w-100 h-100 mx-auto py-4 px-6 flex flex-column flex-start">
                  <a onClick={this.openFormModal} className="apply_now">
                    APPLY NOW
                  </a>
                  <br />
                  <span className="apply_now_text">
                    Application period end date: 05.12.2018, 12 pm
                  </span>
              <div className="block z-1 hero__btn">
                <span className="color_2">
                  <br />
                  Application period:{" "}
                  <span className="bold color_3">5 November - 11 December</span>
                </span>
              </div>
            </div>
          )}
          {this.props.removeText && (
            <div className="hero__inner w-100 h-100  flex flex-end flex-center black" />
          )}
        </div>
      </div>
    );
  }
}



